*
    margin: 0
    padding: 0
table
    border-collapse: collapse
    line-height: 40px
    tr
        &:nth-child(odd)
            background-color: #fafafa
    td
        padding: 0 10px
        &.status
            color: #aaa
        &.red
            animation: 2s infinite red
        &.green
            color: #18a718
        &.colon
            padding: 0
@keyframes red
    0%
        color: #aaa
    50%
        color: #fc5d5d
    100%
        color: #aaa